/*-----  CSS RESET BELOW  -----*/

@import url('https://fonts.googleapis.com/css?family=Roboto');
@import url('https://fonts.googleapis.com/css?family=Righteous');
@import url('https://fonts.googleapis.com/css?family=Caveat');
@font-face {
  font-family: "HelveticaNeueMedium";
  src: url('./fonts/HelveticaNeueMedium.woff') format('woff');
}
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
  outline: none;
}
html {
  height: 100%;
  scroll-behavior: smooth;
}
body {
  height: 100%;
  background-color: #232323;
  overflow-x: hidden;
  overflow-y: hidden;
}
@-moz-document url-prefix() {
  html, body { overflow-x: hidden !important; overflow-y: hidden !important; scrollbar-width: none; }
}
::-webkit-scrollbar {
  display: none; 
}
li {
  list-style: none;
}
a {
  color: #00ffff;
}
button {
  padding: 1vmin;
  background-color: #ffffff;
  border: 2px solid #808080;
  color: #808080;
  cursor: pointer;
}
input {
  padding: 1vmin;
}
::placeholder {
  color: #808080;
}
input:focus::-webkit-input-placeholder {
  color: transparent;
}
input:focus:-moz-placeholder {
  color: transparent;
}
input:focus::-moz-placeholder {
  color: transparent;
}
input:focus:-ms-input-placeholder {
  color: transparent;
}
textarea {
  padding: 1vmin;
  overflow: hidden;
}
textarea:focus::-webkit-input-placeholder {
  color: transparent;
}
textarea:focus:-moz-placeholder {
  color: transparent;
}
textarea:focus::-moz-placeholder {
  color: transparent;
}
textarea:focus:-ms-input-placeholder {
  color: transparent;
}
select {
  padding: 1vmin;
}
@keyframes opacity-delay {
  0%   { opacity: 0 }
  100% { opacity: 1 }
}

/*-----  TRANSITION ANIMATION BELOW  -----*/

.transition-animation {
  overflow: hidden;
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  z-index: 99;
  animation-name: animation-wrapper;
  animation-duration: 4s;
  animation-fill-mode: forwards;
}

@keyframes animation-wrapper {
  0%   {  }
  100% { display: none; z-index: -10;}
}

.loader {
  position: absolute;
  bottom: 0%;
  left: 50%;
  transform: translateX(-50%);
  height: 0.7vmin;
  width: 20%;
  overflow: hidden;
  background-color: #0f0f0f;
}

.loader-bar {
  height: 100%;
  width: 0%;
  overflow: hidden;
  background-color: #00ffff;
  animation: loading 0.9s ease-in-out;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
}

@keyframes loading {
  0%     { width: 0% }
  86%    { width: 100%; background-color: #00ffff }
  100%   { width: 100%; background-color: #0c0c0c }
}

.anim-box1 {
  position: absolute;
  bottom: 7%;
  left: 50%;
  transform: translateX(-50%);
  border: 0.6vmin solid #00ffff;
  width: 5vmin;
  height: 5vmin;
}

.anim-box2 {
  position: absolute;
  top: 5%;
  left: 50%;
  transform: translateX(-50%);
  border: 0.6vmin solid #ffff00;
  width: 5vmin;
  height: 5vmin;
}

.open-top {
  position: absolute;
  top: -50vh;
  width: 100%;
  height: 50.1vh;
  background: #0c0c0c;
  animation-name: open-top;
  animation-delay: 0s;
  animation-duration: 4s;
  animation-fill-mode: forwards;
}

.open-bottom {
  position: absolute;
  bottom: -50vh;
  width: 100%;
  height: 50.1vh;
  background: #0c0c0c;
  animation-name: open-bottom;
  animation-delay: 0s;
  animation-duration: 4s;
  animation-fill-mode: forwards;
}

@keyframes open-top {
  0%   { transform: translateY(0vh) }
  10%  { transform: translateY(50vh) }
  40%  { transform: translateY(50vh) }
  100% { transform: translateY(-50vh) }
}

@keyframes open-bottom {
  0%   { transform: translateY(0vh) }
  10%  { transform: translateY(-50vh) }
  40%  { transform: translateY(-50vh) }
  100% { transform: translateY(50vh) }
}

/*-----  NAVBAR.JS BELOW  -----*/

.navbar {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 7vmin;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #0f0f0f;
  z-index: 99;
}

.navbar a { /* (.navbar a) is actually <Navlink> in JSX  */
  color: #494949;
  width: 7vmin;
  padding: 2.9vmin 0vmin;
  font-size: 2.4vmin;
  animation: opacity-delay;
  animation-delay: 2s;
  animation-duration: 0.9s;
  animation-fill-mode: forwards;
  opacity: 0; 
}

.navbar a:hover {
