  z-index: 1;
}

.box1-workflows, .box2-workflows, .box3-workflows, .box4-workflows {
  animation: opacity-delay;
  animation-delay: 2.7s;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}

.cards-container {
  position: absolute;
  top: calc(50vh - 30vmin); /* grid container height: 48vmin + 12vmin = 60vmin - 50% = 30vmin (this centers it no matter what screen resolution) */
  left: 0%;
  width: 100%;
  height: 100%;
  display: grid;
  text-align: center;
  grid-template-columns: 26vw 26vw 26vw;
  grid-template-rows: 16vmin 16vmin 16vmin; /* 3 rows = 48vmin */
  grid-row-gap: 6vmin; /* there are two gaps = 12vmin */
  justify-content: center;
  opacity: 0;
  animation: opacity-delay;
  animation-delay: 2.3s;
  animation-duration: 0.5s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
}

.card .face {
  display: inline-block;
  width: 12vw;
  height: 16vmin;
  overflow: hidden;
  transition: 0.9s;
}

.card .face.face1 {
  position: relative;
  transform: translateX(6vw);
  display: inline-block;
  background: #0f0f0f;
  z-index: 1;
}

.card .face.face2 {
  position: relative;
  transform: translateX(-6vw);
  display: inline-block;
  padding: 0vmin 2vmin;
  background: #0f0f0f;
  z-index: 0;
}

.card:hover .face.face1 {
  transform: translateX(0);
}

.card:hover .face.face2 {
  transform: translateX(0);
}

.card-content {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  transition: 0.9s;
}

.card-text {
  font-size: 1.4vmin;
  color: white;
  text-align: left;
}

.css-logo, .react-logo, .sass-logo, .nodejs-logo, .npm-logo, .redux-logo {
  height: 9.6vmin;
}

.js-logo, .graphql-logo, .webpack-logo {
  height: 7vmin;
}

